
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}
p{
    font-size: 35px;
    font-weight: bold;
}

/*============CTA================*/

.cta{
    margin-top: 2rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
.header__socials{
display: flex;
flex-direction: column;
gap: 0.4rem;
position: absolute;
left: 0;
bottom: -20rem;
align-items: center;
}
.header__socials a{
    font-size: 26px;
    color: black;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
.me{
   margin-top: 2rem;
    width: 20rem;
    height: 20rem;
    position: absolute;
    left: calc(50% - 10rem);
    border-radius: 50% ;
    box-shadow: -12px 0px 18px 0px rgb(194, 194, 74);
    transition: var(--transition);
}
.me:hover{
    margin-top: 2rem;
    width: 20rem;
    height: 20rem;
    position: absolute;
    left: calc(50% - 10rem);
    border-radius: 50% ;
    box-shadow: 8px 6px 14px 4px  var(--color-primary);

}
.scroll__down{
    display: flex;
    position: absolute;
    right: 0;
    bottom: -20rem;
    font-size: 1.8rem;
   
}
.btn{
    color: black;
    border: 1px solid goldenrod;
    font-weight: 600;
}
.btn-primary{
    background-color: rgb(222, 222, 43);
}
/*===================== medium screen ===================*/
@media screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
   
}
/*===================== small screen  ===================*/

@media screen and (max-width:600px) {
    .header__socials,.scroll__down{
        display: none;
    }
}
.type{
    color: var(--color-primary);
    font-size: 17px;
    color: white;
    font-weight: 700;
}
.header__section{
    margin-bottom: 22rem;
}